import { useState } from "react";
import { flushSync } from "react-dom";
import QuizPage from "./Component/english_quiz/Component/QuizPage";
function App() {
  const [state, setState] = useState({});

  const changeQuestion = (data) => {
    flushSync(() => {
      setState({});
    });
    setState({ ...data });
  };
  window.changeQuestion = changeQuestion;
  return <>{Object.keys(state).length ? <QuizPage obj={state} /> : ""}</>;
}

export default App;
